<template>
  <div class="navbar">
    <hamburger :toggle-click="toggleSideBar" :is-active="sidebar.opened" class="hamburger-container" />
    <breadcrumb />
    <dropdown-message :dropdownItemList="dropdownItemList"></dropdown-message>
    <el-dropdown class="avatar-container" trigger="click">
      <div class="avatar-wrapper">
        <img :src="userInfo.avatar+'?imageView2/1/w/80/h/80'" class="user-avatar">
        <i class="el-icon-caret-bottom"/>
      </div>
      <el-dropdown-menu slot="dropdown" class="user-dropdown">
        <router-link class="inlineBlock" to="/">
          <el-dropdown-item>
            Home
          </el-dropdown-item>
        </router-link>
        <el-dropdown-item divided>
          <span style="display:block;" @click="logout">LogOut</span>
        </el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Vue from 'vue'
import Breadcrumb from '../../components/Breadcrumb'
import DropdownMessage from '../../components/DropdownMessage'
import Hamburger from '../../components/Hamburger'
import { Dropdown, DropdownMenu, DropdownItem } from 'element-ui'

Vue.use(Dropdown)
Vue.use(DropdownMenu)
Vue.use(DropdownItem)
export default {
  components: {
    Breadcrumb,
    Hamburger,
    DropdownMessage
  },
  props: {
    sidebar: {
      type: Object,
      default: () => ({ opened: false, withoutAnimation: false })
    },
    userInfo: {
      type: Object,
      default: () => ({})
    },
    dropdownItemList: {
      type: Array,
      default: () => ([])
    }
  },
  data () {
    return {
      // sidebar: {
      //   opened: true,
      //   withoutAnimation: false
      // },
      // avatar: ''
    }
  },
  computed: {
    ...mapGetters([
      // 'sidebar',
      // 'avatar'
    ])
  },
  methods: {
    toggleSideBar () {
      // this.$store.dispatch('ToggleSideBar')
      // ToggleSideBar()
      this.$emit('toggleSideBar')
    },
    logout () {
      // this.$store.dispatch('LogOut').then(() => {
      //   location.reload() // 为了重新实例化vue-router对象 避免bug
      // })
      this.$emit('logout')
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.navbar {
  height: 50px;
  line-height: 50px;
  box-shadow: 0 1px 3px 0 rgba(0,0,0,.12), 0 0 3px 0 rgba(0,0,0,.04);
  .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
  .screenfull {
    position: absolute;
    right: 90px;
    top: 16px;
    color: red;
  }
  .avatar-container {
    height: 50px;
    display: inline-block;
    position: absolute;
    right: 35px;
    .avatar-wrapper {
      cursor: pointer;
      margin-top: 5px;
      position: relative;
      line-height: initial;
      .user-avatar {
        width: 40px;
        height: 40px;
        border-radius: 10px;
      }
      .el-icon-caret-bottom {
        position: absolute;
        right: -20px;
        top: 25px;
        font-size: 12px;
      }
    }
  }
}
</style>
